body {
  background-color: black;
}

.gifs {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0.5rem;
  margin: 0 1.5rem;
}
.gif {
  margin: 0.5rem;
}

.form {
  display: flex;
  align-items: center;
  justify-content: center;
}
.form input {
  border: none;
  width: 15rem;
  height: 1.6rem;
  padding: 0.6rem;
  margin-top: 1.2rem;
  outline: none;
}

button {
  border: none;
  padding: 0.6rem 1rem;
  font-size: 1.2rem;
  margin-top: 1.2rem;
  background: rgba(41, 131, 214, 1);
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  animation: change 5s infinite;
  outline: none;
  cursor: pointer;
}

@keyframes change {
  0% {
    background: rgba(41, 131, 214, 1);
  }

  50% {
    background: rgba(52, 235, 125, 1);
  }

  100% {
    background: rgba(83, 179, 255, 1);
  }
}

input::placeholder {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.1rem;
}
.fa-bolt {
  background: linear-gradient(
    45deg,
    rgba(43, 153, 255, 1) 22%,
    rgba(3, 201, 255, 1) 100%
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.heading {
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bold;
  font-size: 2.5rem;
  text-align: start;
  margin-left: 4rem;
  position: relative;
}

@media screen and (max-width: 600px) {
  .heading {
    text-align: center;
    margin-left: 0;
  }
}
